.catalogModal {
  height: 100vh;
  width: 100vw;
  padding: 30px 120px;
  max-width: 1440px;
  margin: 0 auto;
  /* background-color: #fff; */

  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  opacity: 0;
  transition: all 0.2s linear;
  pointer-events: none;
  z-index: 100;
}
.catalogModal.active {
  opacity: 1;
  pointer-events: all;
}
.catalogModal__content {
  border-radius: 12px;
  transform: scale(0.5);
  transition: all 0.2s linear;
  width: 100%;
  height: auto;
  padding: 30px;
  background-color: #fff;
  box-shadow: 0px 10px 16px rgba(0, 0, 0, 0.05);
  border-radius: 10px;
  color: black;
  margin-top: 100px;
}
.catalogModal__content.active {
  transform: scale(1);
  overflow: hidden;
}

/* *********************************  MEDIA QUERY  ************************************* */

/* *********************************  max-width: 1400px  *************************************** */
@media (max-width: 1400px) {
  .catalogModal.active {
    padding: 30px 50px;
  }
}
/* *********************************  max-width: 1200px  *************************************** */
@media (max-width: 1400px) {
  .categoriesPage__content {
    justify-content: space-around;
  }
}

/* *********************************  max-width: 992px  *************************************** */
@media (max-width: 992px) {
  .catalogModal.active {
    padding: 30px 30px;
  }
  .catalogModal__content {
    padding: 10px;
  }
}

/* *********************************  max-width: 768px  *************************************** */
@media (max-width: 768px) {
  .catalogModal.active {
    padding: 30px 20px;
  }
}
